/* fonts */
@font-face {
  font-family: "ptsans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ptsans";
  src: local("PT Sans bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
/* common */
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.modal-overlay {
  overflow-y: hidden;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 320px;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
}
.modal__headline {
  margin: 0;
  padding: 38px 38px 45px;
}
.modal__title {
  margin: 0 0 25px;
}
.modal__text {
  margin: 0;
  line-height: 24px;
}
.modal__footer {
  height: 98px;
  padding: 16px 20px;
  text-align: center;
  background-color: #f2f2f2;
}
.modal__close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 58px;
  padding: 0;
  margin: 0 auto;
  font-family: "ptsans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  background-color: #81B3D2;
}
.modal--success {
  height: 323px;
}
.modal--fail {
  height: 280px;
}
.modal--fail .modal__headline {
  padding: 38px 42px 24px;
}
.modal--fail .modal__close-btn {
  width: 180px;
}
@media (min-width: 768px) {
  .modal {
    width: 640px;
    min-height: 347px;
  }
  .modal__headline {
    padding: 54px 113px 45px;
  }
  .modal__title {
    margin-bottom: 31px;
  }
  .modal__close-btn {
    width: 458px;
    font-size: 21px;
    line-height: 26px;
    font-weight: 700;
  }
  .modal__footer {
    height: 146px;
    padding: 42px 20px;
  }
  .modal--fail {
    width: 400px;
    height: 347px;
  }
  .modal--fail .modal__headline {
    padding: 48px 50px 36px;
  }
  .modal--fail .modal__title {
    padding: 0 25px;
  }
  .modal--fail .modal__close-btn {
    width: 272px;
  }
}
.title {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
}
.title--subtitle {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  color: #333333;
}
.title--inner {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.title--subtitle-inner {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #333333;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
    line-height: 26px;
    font-weight: 700;
  }
  .title--subtitle {
    font-size: 14px;
    line-height: 26px;
    font-weight: 400;
  }
  .title--inner {
    font-size: 30px;
    line-height: 26px;
    font-weight: 700;
  }
  .title--subtitle-inner {
    font-size: 14px;
