    color: #333333;
  }
  .title--big {
    font-size: 30px;
    line-height: 40px;
    font-weight: 700;
  }
}
.page__body {
  position: relative;
  margin: 0;
  font-family: "ptsans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  background-color: #f2f2f2;
}
.page__body-wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
  overflow: hidden;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body-wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__body-wrapper {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(1, 1, 1, 0.2);
  }
}
.header__navigation {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 56px;
  background-color: #ffffff;
}
.header__logo {
  position: absolute;
  top: auto;
  bottom: -28px;
  left: 50%;
  transform: translateX(-50%);
  order: 2;
  z-index: 2;
}
.header__toggle {
  position: absolute;
  top: 6px;
  right: 20px;
  transform: translateY(50%);
  display: block;
  width: 23px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/icon-menu.svg");
  cursor: pointer;
}
.header__toggle:hover {
  opacity: 0.6;
}
.header__toggle:active {
  opacity: 0.3;
}
.header__toggle--open {
  top: 5px;
  transform: translateY(50%);
  background-image: url("../img/icon-cross.svg");
}
.header__toggle--nojs {
  display: none;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header__logo {
    bottom: -40px;
  }
  .header__logo-image {
    width: 187px;
    height: 97px;
  }
}
.main--form {
  margin-bottom: 36px;
  background-color: #ffffff;
}
.main--form .main__headline {
  padding: 38px 0 19px;
}
.main--form .main__title {
  margin: 0 0 27px;
}
.main--form .main__text {
  margin: 0;
