  }
  .main--form .main__headline {
    padding: 65px 166px;
  }
  .main--form .main__title {
    margin-bottom: 33px;
    font-size: 30px;
    line-height: 26px;
    font-weight: 700;
  }
  .main--catalog {
    margin-bottom: 39px;
  }
}
@media (min-width: 1200px) {
  .main--form {
    margin-bottom: 65px;
  }
  .main--form .main__headline {
    padding: 112px 380px 40px;
  }
  .main--catalog {
    margin-bottom: 85px;
  }
}
.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-bottom: 56px;
}
.menu--close {
  display: none;
}
.menu__item:last-child > .menu__link {
  box-shadow: inset 0 -7px 7px rgba(1, 1, 1, 0.2);
}
.menu__link {
  display: block;
  max-height: 56px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 18px;
  line-height: 55px;
  font-weight: 700;
  color: #ffffff;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  border-top: 1px solid rgba(153, 151, 151, 0.2);
  background-color: #81B3D2;
}
.menu__link:hover {
  background-color: #669EC0;
}
.menu__link:active {
  background-color: #5496bd;
}
.menu__link:active > .menu__link {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    min-height: 56px;
    padding: 0;
    background-color: #ffffff;
  }
  .menu--close {
    display: flex;
  }
  .menu__item {
    width: 20%;
    padding-left: 2px;
  }
  .menu__item:last-child > .menu__link {
    box-shadow: none;
  }
  .menu__item:nth-child(4n+2) {
    margin-right: 20%;
  }
  .menu__link {
    width: 153px;
    border: none;
    background-color: #ffffff;
    box-shadow: none;
    font-size: 14px;
    line-height: 26px;
    font-weight: 700;
    color: #000000;
  }
  .menu__link:hover {
    color: #81B3D2;
    background-color: #ffffff;
  }
  .menu__link:active {
    color: #000000;
    background-color: #ffffff;
    opacity: 0.3;
  }
  .menu__link--active {
    color: #766357;
    cursor: pointer;
  }
  .menu__link--active:hover {
    color: #604e43;
  }
  .menu__link--active:active {
    opacity: 0.6;
  }
}
@media (min-width: 1200px) {
  .menu__item {
    padding-left: 43px;
  }
}
