@media (min-width: 1200px) {
  .intro {
    padding-top: 77px;
  }
  .intro--small {
    height: 301px;
    padding-top: 102px;
    background-image: url("../img/inner-intro-back-desktop.jpg");
  }
  .intro--small .intro__text {
    width: 464px;
    height: 80px;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .intro--small {
    background-image: url("../img/inner-intro-back-desktop@2x.jpg");
  }
