.features {
  padding-top: 34px;
  background-color: #ffffff;
}
.features__headline {
  margin: 0 auto;
}
.features__title {
  margin: 0 0 20px;
  padding: 0 25px;
}
.features__text {
  margin: 0;
  padding: 10px 25px;
  line-height: 26px;
}
.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.features__item {
  position: relative;
  width: 100%;
}
.features__item:first-child::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  width: 768px;
  height: 37px;
  background-image: url("../img/decorate-triangle.svg");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .features__headline {
    padding: 10px 0 44px;
  }
  .features__title {
    margin-bottom: 21px;
    padding: 0 100px;
  }
  .features__list {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    grid-template-rows: minmax(256px, max-content);
  }
  .features__item {
    grid-column: span 1;
    min-height: 256px;
  }
  .features__item.feature--expanded-full {
    grid-column: 1 / span 3;
    grid-row-start: 1;
    grid-row-end: 3;
    background-color: #ffffff;
  }
  .features__item.feature--expanded-short {
    grid-column: 1 / span 3;
    grid-row: 3 / span 1;
  }
  .features__item:first-child::before {
    display: none;
  }
}
@media (min-width: 1200px) {
  .features__headline {
    padding: 22px 260px 33px;
  }
}
.feature {
  text-align: center;
  background-color: #eeeeee;
}
.feature__headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 35px 25px;
  text-transform: uppercase;
  background-color: #eeeeee;
}
.feature:not(.feature--expanded-full):not(.feature--expanded-short)::after {
  content: "";
  display: block;
  width: 280px;
  height: 1px;
  transform: translateX(20px);
  background-color: #d6d6d6;
}
.feature__title {
  margin: 0;
}
.feature__number {
  margin: 10px 0 16px;
}
.feature__description {
  margin: 0;
}
.feature__image {
  max-height: 260px;
  background-color: #ddeaf3;
}
.feature__image-source {
  transform: translateX(-50%);
  margin: 0 50%;
}
.feature__detail-list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 35px 20px;
  text-align: left;
  background-color: #ffffff;
}
.feature__detail {
  position: relative;
  margin: 0 0 25px;
  padding-left: 50px;
}
.feature__detail:last-child {
  margin-bottom: 0;
}
.feature__detail::before {
  content: "";
  display: block;
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
}
.feature__detail--housing::before {
  top: -6px;
  left: 1px;
  width: 39px;
  height: 37px;
  background-image: url("../img/icon-house.svg");
}
.feature__detail--gift::before {
  top: -10px;
  left: 5px;
  width: 32px;
  height: 38px;
  background-image: url("../img/icon-gift.svg");
}
.feature__detail--food::before {
  top: -5px;
  left: -2px;
  width: 40px;
  height: 34px;
  background-image: url("../img/icon-burger.svg");
}
.feature__detail--gift br {
  display: none;
}
.feature__detail-title {
  margin-top: 0;
  text-align: left;
}
.feature__detail-title:last-child {
  margin: 0;
}
.feature__detail-text {
  margin-top: 0;
  text-align: left;
  text-transform: uppercase;
}
.feature__detail-text:last-child {
  margin: 0;
}
.feature--expanded-full .feature__headline {
  padding: 71px 14px 39px;
  color: #ffffff;
  border: none;
  background-color: #81B3D2;
}
.feature--expanded-full .feature__title {
  color: #ffffff;
}
.feature--expanded-full .feature__number {
  margin: 11px 0 16px;
}
.feature--expanded-short .feature__headline {
  padding: 38px 14px 35px;
  margin: 0;
  color: #ffffff;
  border: none;
  background-color: #81B3D2;
}
.feature--expanded-short .feature__title {
  color: #ffffff;
}
.feature--expanded-short .feature__number {
  margin: 13px 0 14px;
}
@media (min-width: 768px) {
  .feature:not(.feature--expanded-full):not(.feature--expanded-short)::after {
    display: none;
  }
  .feature__headline {
    height: 100%;
    padding: 41px 38px 45px;
  }
  .feature__title {
    padding: 0 48px;
  }
  .feature__number {
    margin: 14px 0 23px;
  }
  .feature__description--narrow {
    padding: 0 16px;
  }
  .feature--expanded-full {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
  }
  .feature--expanded-full > .feature__headline {
    grid-column: 1 / span 1;
    padding: 40px 30px;
  }
  .feature--expanded-full .feature__number {
    margin: 13px 0 23px;
  }
  .feature--expanded-full > .feature__image {
    grid-column: 2 / span 2;
  }
  .feature--expanded-full > .feature__detail-list {
    grid-column: 1 / span 3;
  }
  .feature__detail-list {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    align-content: end;
    padding: 158px 0 60px;
  }
  .feature__detail {
    margin: 0;
    padding: 0;
  }
  .feature__detail--housing {
    grid-column: 1;
  }
  .feature__detail--housing .feature__detail-text {
    padding: 0 33px;
  }
  .feature__detail--gift {
    grid-column: 3;
  }
  .feature__detail--gift .feature__detail-text {
    padding: 0 30px 0 33px;
  }
  .feature__detail--food {
    grid-column: 2;
    grid-row: 1;
  }
  .feature__detail--food .feature__detail-text {
    padding: 0 48px 0 50px;
  }
  .feature__detail::before {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-repeat: no-repeat;
    background-size: cover;
  }
  .feature__detail--housing::before {
    top: -98px;
    width: 75px;
    height: 72px;
  }
  .feature__detail--gift::before {
    top: -100px;
    width: 64px;
    height: 75px;
  }
  .feature__detail--food::before {
    top: -95px;
    width: 76px;
    height: 68px;
  }
  .feature__detail-title {
    text-align: center;
    margin-bottom: 23px;
  }
  .feature__detail-text {
    text-align: center;
  }
  .feature--expanded-short {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
  }
  .feature--expanded-short > .feature__headline {
    grid-column: 3 / span 1;
    padding: 42px 40px 0;
  }
  .feature--expanded-short .feature__title {
    padding: 0 13px;
  }
  .feature--expanded-short .feature__number {
    margin: 14px 0 23px;
  }
  .feature--expanded-short > .feature__image {
    grid-column: 1 / span 2;
    order: -1;
  }
  .feature--expanded-short > .feature__detail-list {
    grid-column: 1 / span 3;
  }
}
@media (min-width: 1200px) {
  .feature__description {
    max-width: 295px;
  }
  .feature__title {
    padding: 0 85px;
  }
  .feature--expanded-full .feature__headline {
    padding: 45px 67px;
  }
  .feature__detail-list {
    padding-bottom: 80px;
  }
  .feature__detail--housing .feature__detail-text {
    padding: 0 45px;
  }
  .feature__detail--gift .feature__detail-text {
    padding: 0 45px 0 51px;
  }
  .feature__detail--gift .feature__detail-text br {
    display: block;
  }
  .feature__detail--food .feature__detail-text {
    padding: 0 45px 0 51px;
  }
  .feature--expanded-short .feature__headline {
    padding: 52px 85px 0;
  }
  .feature--expanded-short .feature__title {
    padding: 0 39px 0 41px;
  }
}
.booking {
