  text-align: center;
  background-color: #ffffff;
}
.booking__headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
}
.booking__title {
  margin: 0 0 20px;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
