.map {
  text-align: center;
  background-color: #d6d6d6;
}
.map__image {
  display: none;
  min-height: 350px;
  background-repeat: no-repeat;
  background-size: contain;
}
.map__iframe {
  display: block;
  width: 100%;
  border: 0;
}
.footer {
  padding: 40px 45px 26px;
  text-transform: uppercase;
  background-color: #ffffff;
}
.footer__hashtag {
  margin: 0;
  text-align: center;
