    margin: 0 0 0 112px;
  }
  .controls__button--replay {
    order: -1;
    margin: 0;
    color: #ffffff;
  }
  .controls__button--subtitles {
    color: #ffffff;
  }
  .controls__button--fullscreen {
    color: #ffffff;
  }
}
@media (min-width: 1200px) {
  .controls {
    width: 800px;
  }
  .controls__progress-wrapper {
    width: 540px;
  }
  .controls__progress {
    width: 540px;
  }
  .controls__track {
    width: 540px;
  }
}
